.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0.3);
  visibility: hidden;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.overlayVisible {
  visibility: visible;
  opacity: 1;

  .drawer {
    transform: translateX(0);
  }
}

.drawer {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 420px;
  height: 95%;
  right: 0;
  background: #fff;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  padding: 30px;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;

  .items {
    flex: 1;
    overflow: auto;
    margin-bottom: 40px;
  }

  h2 {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;

    img {
      width: 50px;
      cursor: pointer;
    }
  }

  .cartEmpty {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;

    img {
      width: 150px;
    }

    p {
      margin-bottom: 50px;
      text-align: center;
      opacity: 0.5;
    }

    button {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 55px;
      background: #9dd558;
      border-radius: 18px;
      border: 0;
      color: #fff;
      font-size: 16px;
      font-weight: 600;
      cursor: pointer;
      transition: background 0.3s ease-in-out;

      &:hover {
        background:#3f3f3f;
      }
      img {
        width: 50px;
        transform: rotateY(180deg);
      }
    }

  }

  .cartItem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #3f3f3f;
    border-radius: 20px;
    padding: 10px;
    margin-bottom: 20px;
    overflow: hidden;

    .info {
      margin-right: 15px;
    }

    .removeBtn {
      opacity: 0.5;
      cursor: pointer;
      transition: opacity 0.2s ease-in-out;

        &:hover {
          opacity: 1;
        }
    }

    p {
      font-size: 16px;
    }

    b {
      font-size: 14px;
    }

    img {
      background-position: center center;
      margin-right: 15px;
    }
  }

  .outcome {
    ul {
      padding: 0;
      margin-bottom: 30px !important;

      div {
        flex: 1;
        height: 1px;
        border-bottom: 1px dashed #dfdfdf;
        position: relative;
        top: -6px;
        margin: 0 7px;
      }
      li {
        list-style-type: none;
        display: flex;
        align-items: flex-end;
        margin-bottom: 20px;
      }
    }

    .greenBtn {
      position: relative;
      
      &:disabled {
        background: #bebebe !important;
      }

      &:hover {
        img {
          transform: translateX(5px);
        }
      }

      img {
        position: absolute;
        top: 19px;
        right: 40px;
        transition: transform 0.15s ease-in-out;
      }
    }

    .greenBtn {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 55px;
        background: #9dd558;
        border-radius: 18px;
        border: 0;
        color: #fff;
        font-size: 16px;
        font-weight: 600;
        cursor: pointer;
        transition: background 0.3s ease-in-out;
  
        &:hover {
          background: #3f3f3f;
        }
  
        &:active {
          background: darken(#9dd558, 5%);
      }
    }
  }
}